import { Box, Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, Grid, IconButton, Tab, Tabs, TextField, Typography } from "@mui/material"
import { useEffect, useState } from "react"
import { useParams } from "react-router-dom"
import useGoogleSheets from "use-google-sheets"
import { MainLayout } from "../../../layouts/main"
import SettingsIcon from '@mui/icons-material/Settings';
import { DataGrid } from "@mui/x-data-grid"
import useDrivePicker from 'react-google-drive-picker'

const TabPanel = (props) => {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}

export const ProgramDetails = () => {
    const { id } = useParams()

    const [tabSelected, setTabSelected] = useState(0)
    const [programConfigurationDialogOpened, setProgramConfigurationDialogOpened] = useState(false)
    const [programItemConfigurationDialogOpened, setProgramItemConfigurationDialogOpened] = useState(false)
    const [sheetId, setSheetId] = useState('')
    const [driveId, setDriveId] = useState('') 
    
    const [openPicker, authResponse] = useDrivePicker();  
    
    // const customViewsArray = [new google.picker.DocsView()]; // custom view
    const handleOpenPicker = () => {
      openPicker({
        clientId: "798494193452-f7l4v3ubfephu1i033u2kti7194rec51.apps.googleusercontent.com",
        developerKey: "AIzaSyA4PJsvuPSesKIvHP-pG3Aga0dQJxmNmGA",
        viewId: "DOCS",
        // token: token, // pass oauth token in case you already have one
        showUploadView: true,
        showUploadFolders: true,
        supportDrives: true,
        multiselect: true,
        // customViews: customViewsArray, // custom view
        callbackFunction: (data) => {
          if (data.action === 'cancel') {
            console.log('User clicked cancel/close button')
          }
          console.log(data)
        },
      })
    }
  
    const handleTabSelect = (e, newValue) => {
        setTabSelected(newValue)
    }

    let { data, loading, error } = useGoogleSheets({
        apiKey: "AIzaSyBxXggunVhzxfn8lj2oUjYCpLbWBY1D9xM",
        sheetId: sheetId,
    });

    const HandleLinkSheet = () => {
        setProgramItemConfigurationDialogOpened(false)
    }

    return (
        <MainLayout>
            <Box style={{ display: 'flex', justifyContent: 'space-between', marginBottom: '25px' }}>
                <Typography variant="h5">TechStart 2023</Typography>
                <IconButton onClick={() => setProgramConfigurationDialogOpened(true)}>
                    <SettingsIcon />
                </IconButton>
            </Box>

            <Box >
                <Box sx={{ flexGrow: 1, bgcolor: 'background.paper', display: 'flex', height: 450 }}>
                    <Tabs
                        orientation="vertical"
                        variant="scrollable" value={tabSelected}
                        onChange={handleTabSelect}
                        aria-label="program-items"
                        sx={{ borderRight: 1, borderColor: 'divider' }}
                    >
                        <Tab label="Agro Digital" />
                        <Tab label="Food Innovation" />
                        <Tab label="Sade" />
                        <Tab label="Supply Chain" />
                    </Tabs>
                    <Box style={{ width: '100%' }}>
                        <TabPanel value={tabSelected} index={0}>
                            <>
                                <Box style={{ display: 'flex', justifyContent: 'end' }}>
                                    <IconButton onClick={() => setProgramItemConfigurationDialogOpened(true)}>
                                        <SettingsIcon />
                                    </IconButton>
                                </Box>
                            </>
                            {loading || data.length <= 0 ? (
                                'sem dados'
                            ) : (
                                <>
                                    <Typography variant="h5">Planilha: {data[0].id}</Typography>
                                    <Typography variant="h6">{'Qtd. de Inscries: ' + data[0].data.length}</Typography>
                                    {JSON.stringify(data)}

                                    <Button onClick={handleOpenPicker}>ABRIR DRIVE</Button>
                                </>
                            )}
                        </TabPanel>
                        <TabPanel value={tabSelected} index={1}>
                            <Typography variant="h5">Vertical 2</Typography>
                        </TabPanel>
                        <TabPanel value={tabSelected} index={2}>
                            Sobre Vertical 3
                        </TabPanel>
                    </Box>
                </Box>
            </Box>

            <Dialog
                open={programConfigurationDialogOpened}
                onClose={() => setProgramConfigurationDialogOpened(false)}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {'Configuraes do Programa'}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        Cronograma
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setProgramConfigurationDialogOpened(false)}>Cancelar</Button>
                    <Button onClick={HandleLinkSheet} autoFocus>
                        Salvar
                    </Button>
                </DialogActions>
            </Dialog>

            <Dialog
                open={programItemConfigurationDialogOpened}
                onClose={() => setProgramItemConfigurationDialogOpened(false)}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">
                    {"Configuraes do Item do Programa"}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        <Grid container xl={12}>
                            <Grid item xl={12}>
                                Ao vincular uma planilha, seus registros podero ser acessados nessa tela.
                                <TextField style={{ marginTop: '15px' }} value={sheetId} onChange={e => setSheetId(e.target.value)} id="outlined-basic" label="ID da Planilha" variant="outlined" />
                            </Grid>
                            <Grid item xl={12}>
                                Ao vincular um drive, seus arquivos podero ser acessados aqui.
                                <TextField style={{ marginTop: '15px' }} value={driveId} onChange={e => setDriveId(e.target.value)} id="outlined-basic" label="ID do Drive" variant="outlined" />
                            </Grid>
                        </Grid>
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={() => setProgramItemConfigurationDialogOpened(false)}>Cancelar</Button>
                    <Button onClick={HandleLinkSheet} autoFocus>
                        Vincular
                    </Button>
                </DialogActions>
            </Dialog>
        </MainLayout>
    )
}