
import { Box, Button, FormControl, FormHelperText, Grid, InputLabel, TextField, Typography } from '@mui/material';
import { Input } from 'postcss';
import { Form } from 'react-router-dom';
import { toast } from 'react-toastify';

export const Login = () => {
    const handleSubmit = (e) => {
        e.preventDefault();

        const user = e.target[0].value;
        const pass = e.target[1].value;

        if (user == 'vh.operacoes' && pass == 'vh2023') {
            toast.success("Login realizado com sucesso");
            setTimeout(() => {
                window.location.href = '/'
            }, 2000);
        } else {
            toast.error("Login incorreto");
        }
    }

    return (
        <Box style={{ backgroundImage: 'url(/background.jpeg)', backgroundSize: 'cover', height: '100vh', backgroundRepeat: 'no-repeat', margin: '-8px' }}>
            <Box style={{ backgroundColor: 'rgb(0,101,168, 0.7)', backgroundSize: 'cover', height: '100vh', }}>
                <Box style={{ display: 'flex', justifyContent: 'center' }}>
                    <Box xl={12} sm={12} style={{ marginTop: '12%', padding: '35px', backgroundColor: 'white', borderRadius: '15px' }}>
                        <Grid item xl={12} style={{ display: 'flex', justifyContent: 'center', marginBottom: '42px' }}>
                            <Typography variant='h5'>Venture Hub Operaes</Typography>
                        </Grid>
                        <Grid item xl={12}>
                            <Box
                                component="form"
                                sx={{
                                    '& .MuiTextField-root': { m: 1, width: '25ch' },
                                }}
                                noValidate
                                autoComplete="off"
                            >
                                <FormControl fullWidth>
                                    <TextField style={{ width: '100%' }} label='Usurio' />
                                </FormControl>
                                <FormControl fullWidth>
                                    <TextField type='password' style={{ width: '100%' }} label='Senha' />
                                </FormControl>

                                <FormControl fullWidth>
                                    <Button style={{ backgroundColor: '#0d3478', color: 'white', width: '100%', marginLeft: '8px', marginTop: '30px' }} onClick={(e) => handleSubmit(e)} variant='contained'>Entrar</Button>
                                </FormControl>
                            </Box>
                        </Grid>
                    </Box>
                </Box>
            </Box>
        </Box>
    )
}