import React from 'react';
import ReactDOM from 'react-dom/client';
import reportWebVitals from './reportWebVitals';
import { Landing, Subscriptions, Login, Companies, CompanyDetails, Programs, ProgramDetails, Persons, PersonDetails } from './pages';
import 'react-toastify/dist/ReactToastify.css';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import { Subscribe } from './pages/subscriptions/subscribe';

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <ToastContainer
      position="top-right"
      autoClose={1200}
      hideProgressBar={false}
      newestOnTop={false}
      closeOnClick
      rtl={false}
      pauseOnFocusLoss
      draggable
      pauseOnHover
      theme="light"
    />
    <BrowserRouter>
      <Routes>
        <Route path="/" element={<Landing />} />
        <Route path="/login" element={<Login />} />
        <Route path="/subscriptions" element={<Subscriptions />} />

        <Route path="/subscribe" element={<Subscribe />} />
        
        <Route path="/programs" element={<Programs />} />
        <Route path="/program/:id/details" element={<ProgramDetails />} />
        
        <Route path="/companies" element={<Companies />} />
        
        <Route path="/persons" element={<Persons />} />
        <Route path="/person/:id/details" element={<PersonDetails />} />

        <Route path="/company/:id/details" element={<CompanyDetails />} />
      </Routes>
    </BrowserRouter>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
