import { Box, Button, Chip, Typography } from "@mui/material";
import { MainLayout } from "../../layouts/main"
import { DataGrid } from '@mui/x-data-grid';
import { Link } from "react-router-dom";
import { useState } from "react";

export const Persons = () => {
    const [selectedRows, setSelectedRows] = useState([])
    const [mailList, setMailList] = useState('')

    const columns = [
        { field: 'id', headerName: 'ID', width: 90 },
        {
            field: 'name',
            headerName: 'Nome',
            width: 150,
            editable: true,
        },
        {
            field: 'email',
            headerName: 'Email',
            width: 200,
            editable: true,
            renderCell: rowData => {
                return [
                    <a href={`mailto:${rowData.row.email}`}>{rowData.row.email}</a>
                ]
            }
        },
        {
            field: 'telefone',
            headerName: 'Telefone',
            width: 150,
            editable: true,
            renderCell: rowData => {
                const telefone = rowData.row.telefone
                let telefoneWithoutFormat = telefone.replace('(', '').replace(')', '').replace('-', '').replace(' ', '')

                return [
                    <a target='_blank' href={`https://wa.me/${telefoneWithoutFormat}`}>{telefone}</a>
                ]
            }
        },
        {
            field: 'role',
            headerName: 'Tags',
            width: 150,
            editable: true,
            renderCell: rowData => {
                return [
                    <>
                        <Chip label="speaker" color="primary" />
                        <Chip label="mentor" color="primary" />
                    </>
                ]
            }
        },
        {
            headerName: '',
            type: 'actions',
            width: 300,
            align: 'right',
            renderCell: rowData => {
                return [
                    <Button variant="text"><Link style={{ textDecoration: 'none' }} to='/person/Giseli-Fernandes/details'>Ver Mais</Link></Button>
                ]
            }
        },
    ];

    const rows = [
        { id: 1, name: 'Pedro Pimentel', email: 'pedro@venturehub.com', telefone: '(19) 99185-9539', role: 'a' },
        { id: 2, name: 'Maurcio Duran', email: 'mduran@venturehub.com', telefone: '(19) 99185-9539', role: 'a' },
        { id: 3, name: 'Jos Eduardo Azarite', email: 'azarite@venturehub.com', telefone: '(19) 99185-9539', role: 'a' },
        { id: 4, name: 'Thiago Presotti', email: 'thiago@venturehub.com', telefone: '(19) 99185-9539', role: 'a' },
        { id: 5, name: 'Giseli Fernandes', email: 'giseli@venturehub.com', telefone: '(19) 99185-9539', role: 'a' },
        { id: 6, name: 'Erico Pastana', email: 'erico@venturehub.com', telefone: '(19) 99185-9539', role: 'a' },
    ];

    return (
        <MainLayout>
            <Box style={{ marginBottom: '25px' }}>
                <Typography variant="h5">Pessoas</Typography>
            </Box>

            {selectedRows.length > 0 ? (
                <Box style={{ marginBottom: '25px' }}>
                    <Button href={`mailto:${mailList}`} variant="contained">Enviar E-mail</Button>
                </Box>
            ) : <></>}

            <Box sx={{ height: 400, width: '100%' }}>
                <DataGrid
                    rows={rows}
                    columns={columns}
                    initialState={{
                        pagination: {
                            paginationModel: {
                                pageSize: 5,
                            },
                        },
                    }}
                    pageSizeOptions={[5]}
                    checkboxSelection
                    onRowSelectionModelChange={(ids) => {
                        const selectedRowsTmp = rows.filter(row => ids.includes(row.id))
                        let emailsList = ''

                        setSelectedRows(selectedRowsTmp)
                        selectedRowsTmp.forEach(person => {
                            emailsList = emailsList + person.email + ','
                        });

                        setMailList(emailsList)
                    }}
                    disableRowSelectionOnClick
                />
            </Box>
        </MainLayout>
    )
}