import { AppBar, Box, Button, Container, IconButton, Menu, MenuItem, Toolbar, Tooltip, Typography } from "@mui/material";
import { Link } from "react-router-dom"
import { toast } from "react-toastify";

import MenuIcon from '@mui/icons-material/Menu';
import { useState } from "react";

export const Navbar = () => {
    const [navMenuOpened, setNavMenuOpened] = useState(false)
    const [profileMenuOpened, setProfileMenuOpened] = useState(false)

    const handleLogout = () => {
        console.log('deslogando')
        toast.success("Deslogado com sucesso");
        setTimeout(() => {
            window.location.href = '/login'
        }, 2000);
    }

    return (
        <AppBar position="absolute" style={{ backgroundColor: '#0065a8' }}>
            <Container maxWidth="xl">
                <Toolbar disableGutters>
                    <Typography
                        variant="h6"
                        noWrap
                        sx={{
                            mr: 2,
                            display: { xs: 'none', md: 'flex' },
                        }}
                    >
                        <Link style={{ textDecoration: 'none', color: 'white' }} to='/'>Venture Hub Operaes</Link>
                    </Typography>

                    <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
                        <IconButton
                            size="large"
                            aria-label="account of current user"
                            aria-controls="menu-appbar"
                            aria-haspopup="true"
                            onClick={() => setNavMenuOpened(true)}
                            color="inherit"
                        >
                            <MenuIcon />
                        </IconButton>
                        <Menu
                            id="menu-appbar"
                            anchorEl={navMenuOpened}
                            anchorOrigin={{
                                vertical: 'bottom',
                                horizontal: 'left',
                            }}
                            keepMounted
                            transformOrigin={{
                                vertical: 'top',
                                horizontal: 'left',
                            }}
                            open={Boolean(navMenuOpened)}
                            onClose={() => setNavMenuOpened(false)}
                            sx={{
                                display: { xs: 'block', md: 'none' },
                            }}
                        >
                            <MenuItem key={'page-1'} onClick={() => setNavMenuOpened(false)}>
                                <Link style={{ textDecoration: 'none' }} to='/programs'>Programas</Link>
                            </MenuItem>
                            <MenuItem key={'page-2'} onClick={() => setNavMenuOpened(false)}>
                                <Link style={{ textDecoration: 'none' }} to='/companies'>Empresas</Link>
                            </MenuItem>
                            <MenuItem key={'page-3'} onClick={() => setNavMenuOpened(false)}>
                                <Link style={{ textDecoration: 'none' }} to='/persons'>Pessoas</Link>
                            </MenuItem>
                        </Menu>
                    </Box>
                    <Typography
                        variant="h5"
                        noWrap
                        sx={{
                            mr: 2,
                            display: { xs: 'flex', md: 'none' },
                        }}
                    >
                        <Link style={{ textDecoration: 'none', color: 'white' }} to='/'>Venture Hub Operaes</Link>
                    </Typography>
                    <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } }}>
                        <Button
                            variant="text"
                            key={'page-1'}
                            onClick={() => setNavMenuOpened(false)}
                            sx={{ my: 2, color: 'white', display: 'block' }}
                        >
                            <Link style={{ textDecoration: 'none', color: 'white' }} to='/programs'>Programas</Link>
                        </Button>
                        <Button
                            variant="text"
                            key={'page-1'}
                            onClick={() => setNavMenuOpened(false)}
                            sx={{ my: 2, color: 'white', display: 'block' }}
                        >
                            <Link style={{ textDecoration: 'none', color: 'white' }} to='/companies'>Empresas</Link>
                        </Button>
                        <Button
                            variant="text"
                            key={'page-1'}
                            onClick={() => setNavMenuOpened(false)}
                            sx={{ my: 2, color: 'white', display: 'block' }}
                        >
                            <Link style={{ textDecoration: 'none', color: 'white' }} to='/persons'>Pessoas</Link>
                        </Button>
                    </Box>

                    <Box sx={{ flexGrow: 0 }}>
                        <MenuItem key={'profile-logout'} onClick={handleLogout}>
                            <Button type='text'><Typography style={{ color: 'white' }} textAlign="center">Sair</Typography></Button>
                        </MenuItem>
                    </Box>
                </Toolbar>
            </Container>
        </AppBar>
    )
}