import { Box, Button, Chip, Typography } from "@mui/material"
import { DataGrid } from "@mui/x-data-grid"
import { Link } from "react-router-dom";
import { MainLayout } from "../../layouts/main"

export const Programs = () => {
    const columns = [
        {
            field: 'name',
            headerName: 'Nome do Programa',
            width: 250,
            editable: true,
        },
        {
            field: 'coordName',
            headerName: 'Coordenador',
            width: 200,
            editable: true,
            renderCell: (p) => {
                return [
                    <Link to='/person/pedro-pimentel/details' style={{ textDecoration: 'none' }} >{p.row.coordName}</Link>
                ]
            }
        },
        {
            type: 'actions',
            headerName: '',
            width: 800,
            align:'right',
            renderCell: (p) => {
                return [
                    <Button variant="text"><Link style={{ textDecoration: 'none' }} to='/program/tech-start/details'>Detalhes</Link></Button>
                ]
            }
        },
    ];

    const rows = [
        { id: 1, name: 'TechStart 2023', coordName: 'Pedro Pimentel', },
        { id: 2, name: 'PUC Mescla', coordName: 'Giseli Fernandes', },
    ];

    return (
        <MainLayout>
            <Box style={{ marginBottom: '25px' }}>
                <Typography variant="h5">Programas</Typography>
            </Box>

            <Box sx={{ height: 400, width: '100%' }}>
                <DataGrid
                    rows={rows}
                    columns={columns}
                    initialState={{
                        pagination: {
                            paginationModel: {
                                pageSize: 5,
                            },
                        },
                    }}
                    pageSizeOptions={[5]}
                    disableRowSelectionOnClick
                />
            </Box>
        </MainLayout>
    )
}